:root {
  --PrimaryColor: hsl(199, 100%,33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --Whitecolor: hsl(0, 0%, 100%);
  --BlackColor: hsl(201, 33%, 16%);
  --TextColor: hsla(240, 5%, 36%, 0.534);
  --whiteColorDeam: hsl(0, 0%, 92%);
  --greyText: rgb(190, 190, 190);
  --InputColor: rgb(239, 238, 239);
  --bodyColor:rgb(240, 240, 246);
  --CardBG: rgb(225, 225, 235);
}

.footer {
  width: 100%;
  position: relative;
  padding: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.footer .videoDiv {
  position: absolute;
  height: 100%;
  width: 100%;
}
.footer .videoDiv video {
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.footer .videoDiv::after {
  content: "";
  position: absolute;
  background: rgba(10, 84, 107, 0.307);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  mix-blend-mode: multiply;
}
.footer .secContent {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 2 rem initial;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  row-gap: 2rem;
  z-index: 100;
}
.footer .secContent .contacDiv {
  justify-content: space-between;
  flex-direction: column;
  gap: 1rem;
}
.footer .secContent .contacDiv .text small {
  font-size: 13px;
  font-weight: 400;
  color: white;
}
.footer .secContent .contacDiv .text h2 {
  color: white;
  font-size: 1.8rem;
}
.footer .secContent .contacDiv .inputDiv {
  width: 100%;
  gap: 1rem;
  flex-direction: column;
}
.footer .secContent .contacDiv .inputDiv input {
  width: 100%;
  padding: 0.5rem 0.6rem;
  border-radius: 3rem;
  border: 1px solid var(--Whitecolor);
  outline: none;
  background: transparent;
  color: var(--Whitecolor);
}
.footer .secContent .contacDiv .inputDiv input::-moz-placeholder {
  color: var(--Whitecolor);
  opacity: 0.5;
}
.footer .secContent .contacDiv .inputDiv input:-ms-input-placeholder {
  color: var(--Whitecolor);
  opacity: 0.5;
}
.footer .secContent .contacDiv .inputDiv input::placeholder {
  color: var(--Whitecolor);
  opacity: 0.5;
}
.footer .secContent .contacDiv .inputDiv .btn {
  width: 100%;
  justify-content: center;
  color: var(--Whitecolor);
  font-weight: 500;
  gap: 0.5rem;
}
.footer .secContent .contacDiv .inputDiv .btn .icon {
  font-size: 18px;
}
.footer .secContent .footerCard {
  position: relative;
  padding: 1rem 1rem 4rem;
  gap: 1rem;
  border-radius: 3rem;
  background: var(--bodyColor);
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}
.footer .secContent .footerCard .footerintro {
  flex-basis: 50%;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 0.6rem;
}
.footer .secContent .footerCard .logoDiv .logo {
  color: var(--BlackColor);
  font-weight: 600;
}
.footer .secContent .footerCard .logoDiv .logo .icon {
  font-size: 25px;
  color: var(--PrimaryColor);
  margin-right: 10px;
}
.footer .secContent .footerCard .footerparagraph {
  font-size: 13px;
  color: var(--TextColor);
}
.footer .secContent .footerCard .footerSocials {
  -moz-column-gap: 0.6rem;
       column-gap: 0.6rem;
}
.footer .secContent .footerCard .footerSocials .icon {
  color: var(--BlackColor);
  font-size: 20px;
}
.footer .secContent .footerCard .footerSocials .icon:hover {
  color: var(--PrimaryColor);
}
.footer .secContent .footerCard .footerLinks {
  flex-basis: 50%;
  width: 100%;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-between;
}
.footer .secContent .footerCard .footerLinks .linkGroup .groupTittle {
  display: block;
  font-weight: 600;
  color: var(--BlackColor);
  margin-bottom: 0.5rem;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList {
  font-size: 15px;
  color: var(--TextColor);
  transition: 0.3s ease-in-out;
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList .icon {
  font-size: 15px;
  color: var(--greyText);
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList:hover {
  color: var(--PrimaryColor);
  transform: translateXKs(7px);
}
.footer .secContent .footerCard .footerLinks .linkGroup .footerList:hover .icon {
  color: var(--SecondaryColor);
}
.footer .secContent .footerCard .footerDiv {
  flex-direction: column;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--SecondaryColor);
  padding: 0.5rem 0.2rem;
}
.footer .secContent .footerCard .footerDiv small {
  font-size: 10px;
  color: var(--Whitecolor);
}

@media screen and (min-width: 500px) {
  .footer .secContent .footerCard .footerLinks {
    grid-template-columns: repeat(3, 1fr);
    padding-bottom: 1rem;
  }
  .footer .secContent .footerCard .footerDiv {
    justify-content: space-between;
    flex-direction: row;
  }
  .footer .secContent .footerCard .footerDiv small {
    font-size: 14px;
    color: var(--Whitecolor);
  }
}
@media screen and (min-width: 660px) {
  .footer .secContent .contacDiv {
    flex-direction: row;
    justify-content: space-between;
  }
  .footer .secContent .contacDiv .inputDiv {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    flex-direction: row;
    justify-content: flex-start;
  }
  .footer .secContent .contacDiv .inputDiv input {
    width: 60%;
  }
  .footer .secContent .contacDiv .inputDiv .btn {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
  }
}
@media screen and (min-width: 660px) {
  .footer {
    padding: 5rem;
  }
  .footer .secContent .footerCard {
    flex-direction: row;
  }
  .footer .secContent .footerCard .footerLinks {
    grid-template-columns: repeat(3, 1fr);
    padding-bottom: 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .footer .secContent .footerCard {
    padding: 3rem 1rem;
  }
  .footer .secContent .footerCard .footerintro .footerparagraph {
    font-size: 15px;
    color: var(--TextColor);
  }
  .footer .secContent .footerCard .footerintro .footerSocials {
    -moz-column-gap: 0.5rem;
         column-gap: 0.5rem;
  }
  .footer .secContent .footerCard .footerintro .footerSocials .icon {
    color: var(--TextColor);
    font-size: 25px;
  }
  .footer .secContent .footerCard .footerintro .footerSocials .icon:hover {
    color: var(--PrimaryColor);
  }
  .footer .secContent .footerCard .footerLinks .linkGroup {
    font-size: 16px;
    padding: 5px 0;
  }
}/*# sourceMappingURL=footer.css.map */