:root {
  --PrimaryColor: hsl(199, 100%,33%);
  --SecondaryColor: hsl(187, 85%, 43%);
  --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
  --Whitecolor: hsl(0, 0%, 100%);
  --BlackColor: hsl(201, 33%, 16%);
  --TextColor: hsl(240, 5%, 36%);
  --whiteColorDeam: hsl(0, 0%, 92%);
  --greyText: rgb(190, 190, 190);
  --InputColor: rgb(239, 238, 239);
  --bodyColor:rgb(240, 240, 246);
  --CardBG: rgb(225, 225, 235);
}

.home {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.home .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgb(47, 106, 127);
  top: 0;
  bottom: 0;
  z-index: 1;
  mix-blend-mode: hard-light;
}
.home video {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  -o-object-fit: cover;
     object-fit: cover;
}
.home .homeContent {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 8rem 1.5rem 3rem;
  display: flex;
  row-gap: 3rem;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  margin: auto;
  color: var(--whiteColorDeam);
  z-index: 100;
}
.home .homeContent .homeContent, .home .homeContent .icon {
  color: var(--BlackColor);
}
.home .homeContent .textDiv {
  padding: 2rem 0;
}
.home .homeContent .textDiv .smallText {
  font-size: 13;
  text-transform: uppercase;
}
.home .homeContent .textDiv .homeTittle {
  position: relative;
  font-size: 1.8rem;
}
.home .homeContent .cardDiv {
  position: relative;
  padding: 2rem 1rem;
  background: var(--Whitecolor);
  border-radius: 10px;
  gap: 1rem;
}
.home .homeContent .cardDiv label {
  display: block;
  color: var(--TextColor);
  padding-bottom: 10px;
  font-size: 13px;
  font-weight: 500;
}
.home .homeContent .input {
  height: 40px;
  padding: 0 1rem;
  background: var(--InputColor);
  border-radius: 3rem;
}
.home .homeContent .input input {
  background: none;
  border: none;
  outline: none;
  width: 100%;
  font-size: 13px;
  font-weight: 500;
}
.home .homeContent .input input::-moz-placeholder {
  font-size: 13px;
}
.home .homeContent .input input:-ms-input-placeholder {
  font-size: 13px;
}
.home .homeContent .input input::placeholder {
  font-size: 13px;
}
.home .homeContent .input input::-moz-placeholder icon {
  font-size: 18px;
}
.home .homeContent .input input:-ms-input-placeholder icon {
  font-size: 18px;
}
.home .homeContent .input input::placeholder icon {
  font-size: 18px;
}
.home .homeContent .searchOptions {
  position: absolute;
  background: var(--gradientColor);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  bottom: -0.7rem;
  -moz-column-gap: 0.8rem;
       column-gap: 0.8rem;
  padding: 5px 1rem;
  border-radius: 3rem;
  left: 50%;
  transform: translate(-50%);
  justify-content: center;
  cursor: pointer;
}
.home .homeContent .searchOptions:hover {
  background: var(--PrimaryColor);
}
.home .homeContent .searchOptions icon {
  font-size: 18px;
}
.home .homeContent .searchOptions span {
  font-size: 13px;
}
.home .homeFooterIcons {
  width: 100%;
  justify-content: space-between;
}
.home .homeFooterIcons .icon {
  font-size: 20px;
  margin: 5px;
  color: var(--Whitecolor);
  font-weight: 600;
}
.home .homeFooterIcons .icon:hover {
  color: var(--PrimaryColor);
}

@media screen and (min-width: 500px) {
  .home .homeContent .smallText {
    font-size: 16px;
  }
  .home .homeContent .homeTittle {
    position: relative;
    font-size: 2.3rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
  }
  .home .homeContent .homeTittle::after {
    position: absolute;
    content: "";
    background: var(--SecondaryColor);
    height: 5px;
    width: 14px;
    right: 0px;
    bottom: 5px;
    z-index: -1;
  }
  .home .cardDiv {
    grid-template-columns: repeat(2.1fr);
  }
}
@media screen and (min-width: 660px) {
  .home .homeContent .cardDiv {
    padding: 2rem 1rem;
    grid-template-columns: repeat(3.1fr);
  }
}
@media screen and (min-width: 940px) {
  .home .homeContent {
    height: 85vh;
  }
}
@media screen and (min-width: 1240px) {
  .home {
    height: 95vh;
  }
  .home .homeContent {
    padding-left: 0;
    padding-right: 0;
  }
  .home .homeContent .cardDiv {
    padding: 2rem 1 rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .home .homeContent .cardDiv .input {
    margin-bottom: 1rem;
  }
  .home .homeContent .cardDiv .searchOptions {
    bottom: -1rem;
    padding: 8px 1rem;
  }
  .home .homeContent .cardDiv .searchOptions span {
    font-size: 17px;
  }
}/*# sourceMappingURL=home.css.map */