:root{
    --PrimaryColor : hsl(199, 100%,33%);
    --SecondaryColor : hsl(187, 85%, 43%);
    --gradientColor : linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --Whitecolor: hsl(0, 0%, 100%);
    --BlackColor : hsl(201, 33%, 16%);
    --TextColor: hsla(240, 5%, 36%, 0.534);
    --whiteColorDeam: hsl(0, 0%, 92%);
    --greyText : rgb(190, 190, 190);
    --InputColor : rgb(239, 238, 239);
    --bodyColor :rgb(240, 240, 246);
    --CardBG : rgb(225, 225, 235);   
}

.footer{
    width: 100%;
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;

    .videoDiv{
        position: absolute;
        height: 100%;
        width: 100%;

        video{
            height: 100%;
            object-fit: cover;
        }

        &::after{
            content:'';
            position: absolute;
            background: rgba(10, 84, 107, 0.307);
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent{
        width: 100%;
        height: max-content;
        padding: 2 rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contacDiv{
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;

            .text{
                small {
                    font-size: 13px;
                    font-weight: 400;
                    color: white;
                }
                h2{
                    color: white;
                    font-size: 1.8rem;
                }
            }

            .inputDiv{
                width: 100%;
                gap: 1rem;
                flex-direction: column;

                input{
                    width: 100%;
                    padding: .5rem .6rem;
                    border-radius: 3rem;
                    border: 1px solid var(--Whitecolor);
                    outline: none;
                    background: transparent;
                    color: var(--Whitecolor);

                    &::placeholder{
                        color: var(--Whitecolor);
                        opacity: .5;
                    }
                }

                .btn{
                    width: 100%;
                    justify-content: center;
                    color: var(--Whitecolor);
                    font-weight: 500;
                    gap: .5rem;

                    .icon{
                        font-size: 18px;
                    }
                }
            }
            
        }
        .footerCard{
            position: relative;
            padding: 1rem 1rem 4rem;
            gap: 1rem;
            border-radius: 3rem;
            background: var(--bodyColor);
            width: 100%;
            justify-content: center;
            flex-direction: column;
            align-items: flex-start;
            overflow: hidden;

            .footerintro{
                flex-basis: 50%;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                row-gap: .6rem;
            
            }

            .logoDiv{
                .logo{
                    color: var(--BlackColor);
                    font-weight: 600;

                    .icon{
                        font-size: 25px;
                        color: var(--PrimaryColor);
                        margin-right: 10px;
                    }
                }
            }

            .footerparagraph{
                font-size: 13px;
                color: var(--TextColor);

            }

            .footerSocials{
                column-gap: .6rem;

                .icon{
                    color: var(--BlackColor);
                    font-size: 20px;

                    &:hover{
                        color: var(--PrimaryColor);
                    }
                }
            }

            .footerLinks{
                flex-basis: 50%;
                width: 100%;
                gap: 1rem;
                grid-template-columns: repeat(2, 1fr);
                justify-content: space-between;

                .linkGroup{
                    .groupTittle{
                        display: block;
                        font-weight: 600;
                        color: var(--BlackColor);
                        margin-bottom: .5rem;
                    }

                    .footerList{
                        font-size: 15px;
                        color: var(--TextColor);
                        transition: .3s ease-in-out;

                        .icon{
                            font-size: 15px;
                            color: var(--greyText);
                        }

                         &:hover{
                            color: var(--PrimaryColor);
                              transform: translateXKs(7px);

                             .icon{
                                    color: var(--SecondaryColor);
                             }
                         }
                        
                    }
                }
            }
            .footerDiv{
                flex-direction: column;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                background: var(--SecondaryColor);
                padding: .5rem .2rem;

                small{
                    font-size: 10px;
                    color: var(--Whitecolor);
                }
            }
        }

    }
}


//media queeries

@media screen and (min-width: 500px){
    .footer{
        .secContent{
            .footerCard{
                .footerLinks{
                    grid-template-columns: repeat(3,1fr);
                    padding-bottom: 1rem;
                }

                .footerDiv{
                    justify-content: space-between;
                    flex-direction: row;

                    small{
                        font-size: 14px;
                        color: var(--Whitecolor);
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 660px){
    .footer{
        .secContent{
           .contacDiv{
            flex-direction: row;
            justify-content: space-between;

            .inputDiv{
                width: max-content;
                flex-direction: row;
                justify-content: flex-start;

                input{
                    width: 60%;
                }

                .btn{
                    width: max-content;
                }
            }
           }
        }
    }
}

@media screen and (min-width: 660px){
    .footer{
        padding: 5rem;
        .secContent{
           .footerCard{
            flex-direction: row;

            .footerLinks{
                grid-template-columns: repeat(3,1fr);
                padding-bottom: 1rem;
            }
           }
        }
    }
}

@media screen and (min-width: 1024px){
    .footer{
       .secContent{
        .footerCard{
            padding: 3rem 1rem;

            .footerintro{
                .footerparagraph{
                    font-size: 15px;
                    color: var(--TextColor);
                }

                .footerSocials{
                    column-gap: .5rem;
                    .icon{
                        color: var(--TextColor);
                        font-size: 25px;
                        &:hover{
                            color: var(--PrimaryColor);
                        }
                    }
                }
            }
            .footerLinks{
                .linkGroup{
                    font-size: 16px;
                    padding: 5px 0;
                }
            }
        }
       }
    }
}