:root{
    --PrimaryColor : hsl(199, 100%,33%);
    --SecondaryColor : hsl(187, 85%, 43%);
    --gradientColor : linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --Whitecolor: hsl(0, 0%, 100%);
    --BlackColor : hsl(201, 33%, 16%);
    --TextColor: hsl(240, 5%, 36%);
    --whiteColorDeam: hsl(0, 0%, 92%);
    --greyText : rgb(68, 67, 67);
    --InputColor : rgb(239, 238, 239);
    --bodyColor :rgb(240, 240, 246);
    --CardBG : rgb(225, 225, 235);
    --cardHover :rgb(183, 245, 170);  
}

.main{
    width: 100%;

    .secTittle{
        .tittle{
            position: relative;
            width: max-content;
            color: var(--TextColor);
            margin: 1rem ;
            z-index: 2 ;

            &::after{
                position: absolute;
                content: '';
                background:  var(--SecondaryColor);
                height: 1px;
                width: 99px;
                right: 0;
                bottom: 5px;
                z-index: -1;
            }
        }
    }

    .secContent{
        justify-content: center;
        gap: 1.5rem ;

        .singleDestination{
            height: 100%;
            display: grid;
            row-gap: 10px;
            padding: 1rem;
            border-radius: 10px;
            align-items: center;
            background: var(--CardBG);
            box-shadow: 0 2px 4px rgba(140, 140,141,0.549);
            overflow: hidden;
            &:hover{
                background: var(--cardHover);
                box-shadow: 1 4px 4px rgba(55, 55, 75, 0.733);
                transition: -3s ease;

            }
            .imageDiv{
                height: 180px;
                width: 100%;
                overflow: hidden;

                img{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    transition: 2s ease;
                }

                &:hover{
                    img{
                        transform: scale(1.1);
                    }
                }
            }

            .cardInfo{
                padding: 1rem;
                .desTittle{
                    color: var(--BlackColor);
                    font-size: 1.25rem;

                }

                .continent{
                    gap: .5rem;
                    font-size: 18px;
                }

                .name{
                    color: var(--TextColor);
                    font-weight: 400;
                    font-size: 14px;
                }
            }

            .fees{
                width: 100%;
                justify-content: space-between;
                padding: 1rem;
                margin: 1rem 0;
                border-top: 1.5px solid var(--greyText);
                border-bottom: 1.5px solid var(--greyText);
                
                .grade{
                    color: var(--TextColor);
                    max-width: 200px;
                    line-height: 20px;
                    display: flex;
                    align-items: center;
                    justify-content: center;


                    small{

                        text-align: center;
                        background: var(--gradientColor);
                        border-radius: 1rem;
                        color: var(--Whitecolor);
                        padding: 1px 8px;
                        font-size: 10px;

                    }
                }

                .price{
                    font-size: 2rem;
                    color: var(--BlackColor);
                }
            }

            .desc{
                font-size: 13.5px;
                color: var(--TextColor);
                font-weight: 500;

            }

            .btn{
                margin-top: 1rem;
                color: var(--Whitecolor);
                font-weight: 600;
                justify-content: space-between;
                gap: .5rem;

                .icon{
                    align-self: center;
                    font-size: 18px;
                }
            }

        }
    }
}

//media queriess

@media screen and (min-width: 500px) {
    .tittle{
        font-size: 1.5rem;
        &::after{
            width: 155px !important;
        }
    }

    .secContent{
        grid-template-columns: repeat(2,1fr);

        .singleDestination{
            height: auto;
        }
    }

}

@media screen and (min-width: 840px){
    .secContent{
        grid-template-columns: repeat(3,1fr);

        .singleDestination{
            height: auto;
        }
    }
}